/* Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

body {
  background-color: #f9f9f9;
  color: #333;
  line-height: 1.6;
}

.portfolio-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

/* Navigation */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 0;
}

.logo {
  font-size: 28px;
  font-weight: 700;
  color: #333;
}

.nav-links {
  display: flex;
  list-style: none;
  gap: 30px;
}

.nav-links a {
  color: #333;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s;
}

.nav-links a:hover {
  color: #4e54c8;
}

/* Hero Section */
.hero-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 80px 0;
}

.hero-content {
  flex: 1;
}

.hero-content h3 {
  font-size: 24px;
  color: #4e54c8;
  margin-bottom: 15px;
}

.hero-content h1 {
  font-size: 48px;
  font-weight: 700;
  margin-bottom: 10px;
}

.hero-content h1 span {
  color: #4e54c8;
}

.hero-content p {
  font-size: 18px;
  margin-bottom: 30px;
  color: #666;
}

.hero-buttons {
  display: flex;
  gap: 20px;
}

.primary-btn, .secondary-btn {
  padding: 12px 30px;
  border-radius: 5px;
  font-weight: 600;
  text-decoration: none;
  transition: all 0.3s;
}

.primary-btn {
  background: #4e54c8;
  color: white;
}

.primary-btn:hover {
  background: #3a3f9e;
  transform: translateY(-3px);
}

.secondary-btn {
  border: 2px solid #4e54c8;
  color: #4e54c8;
}

.secondary-btn:hover {
  background: rgba(78, 84, 200, 0.1);
  transform: translateY(-3px);
}

.hero-image {
  flex: 1;
  text-align: center;
}

.hero-image img {
  width: 350px;
  height: 350px;
  border-radius: 50%;
  object-fit: cover;
  border: 10px solid rgba(78, 84, 200, 0.2);
  box-shadow: 0 0 30px rgba(78, 84, 200, 0.1);
}

/* Section Common Styles */
.section-title {
  font-size: 36px;
  font-weight: 700;
  text-align: center;
  margin-bottom: 50px;
  color: #333;
  position: relative;
}

.section-title::after {
  content: '';
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: #4e54c8;
}

/* About Section */
.about-section {
  padding: 40px 0;
}

.about-section2 {
  padding: 100px 0;
}

.about-text {
  max-width: 800px;
  margin: 0 auto 50px;
  text-align: center;
  color: #666;
}

.skills-container {
  display: flex;
  gap: 50px;
  margin-top: 50px;
}

.skills-column {
  flex: 1;
}

.skills-column h3 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #333;
}

.skills-column ul {
  list-style: none;
}

.skills-column li {
  margin-bottom: 15px;
  color: #666;
}

.services-preview {
  flex: 2;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
}

.service-card {
  background: white;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  transition: transform 0.3s;
}

.service-card:hover {
  transform: translateY(-10px);
}

.service-card h4 {
  color: #4e54c8;
  margin-bottom: 10px;
  font-size: 18px;
}

.service-card p {
  color: #666;
}

/* Services Section */
.services-section {
  padding: 80px 0;
  background: #f0f0f0;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
}

.service-card-full {
  background: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  transition: transform 0.3s;
}

.service-card-full:hover {
  transform: translateY(-10px);
}

.service-card-full h3 {
  color: #4e54c8;
  margin-bottom: 15px;
  font-size: 22px;
}

.service-card-full p {
  color: #666;
  margin-bottom: 20px;
}

.learn-more {
  color: #4e54c8;
  text-decoration: none;
  font-weight: 600;
  display: inline-block;
  border-bottom: 2px solid #4e54c8;
  padding-bottom: 5px;
  transition: all 0.3s;
}

.learn-more:hover {
  color: #3a3f9e;
  border-color: #3a3f9e;
}

/* Portfolio Section */
/* Portfolio Section Updates */
.portfolio-section {
  padding: 80px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.portfolio-items-container {
  width: 100%;
  max-width: 1200px;
  display: flex;
  flex-direction: column;
  gap: 40px; /* This controls the space between items */
}

.portfolio-item {
  background: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  width: 100%;
  transition: transform 0.3s;
}

.portfolio-item:hover {
  transform: translateY(-5px);
}

.portfolio-item h3 {
  color: #4e54c8;
  margin-bottom: 15px;
  font-size: 24px;
}

.portfolio-item p {
  color: #666;
  margin-bottom: 20px;
}

.see-more {
  color: #4e54c8;
  text-decoration: none;
  font-weight: 600;
  display: inline-block;
  border-bottom: 2px solid #4e54c8;
  padding-bottom: 5px;
  transition: all 0.3s;
}

.see-more:hover {
  color: #3a3f9e;
  border-color: #3a3f9e;
}

/* Contact Section */
.contact-section {
  padding: 80px 0;
  background: #f0f0f0;
}

.contact-container {
  display: flex;
  gap: 50px;
  max-width: 1000px;
  margin: 0 auto;
}



.download-cv {
  display: inline-block;
  background: #4e54c8;
  color: white;
  padding: 12px 25px;
  border-radius: 5px;
  text-decoration: none;
  font-weight: 600;
  margin-top: 20px;
  transition: all 0.3s;
}

.download-cv:hover {
  background: #3a3f9e;
  transform: translateY(-3px);
}

.contact-form {
  flex: 1;
}

.contact-form h3 {
  margin-bottom: 20px;
  color: #333;
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 15px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-family: 'Poppins', sans-serif;
}

.contact-form textarea {
  height: 150px;
  resize: vertical;
}

.submit-btn {
  background: #4e54c8;
  color: white;
  border: none;
  padding: 15px 30px;
  border-radius: 5px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.submit-btn:hover {
  background: #3a3f9e;
  transform: translateY(-3px);
}

/* Responsive Design */
@media (max-width: 992px) {
  .hero-section {
    flex-direction: column-reverse;
    text-align: center;
    gap: 40px;
  }

  .hero-buttons {
    justify-content: center;
  }

  .skills-container {
    flex-direction: column;
  }
}

@media (max-width: 768px) {
  .navbar {
    flex-direction: column;
    gap: 20px;
  }

  .hero-content h1 {
    font-size: 36px;
  }

  .hero-image img {
    width: 250px;
    height: 250px;
  }

  .contact-container {
    flex-direction: column;
  }
}

@media (max-width: 576px) {
  .hero-buttons {
    flex-direction: column;
    gap: 15px;
  }

  .hero-image img {
    width: 200px;
    height: 200px;
  }
}

.social-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 18px;
  text-decoration: none;
  color: white;
  font-weight: bold;
  margin: 5px;
  transition: background 0.3s ease-in-out;
}

.github { background: #333; }
.linkedin { background: #0077b5; }
.gmail { background: #d14836; }

.github:hover { background: #555; }
.linkedin:hover { background: #005582; }
.gmail:hover { background: #b23121; }

.social-btn i {
  font-size: 22px;
}
