/* Navbar Styles */
.navbar {
    background-color: transparent;
    padding: 25px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    z-index: 1000;
    transition: all 0.3s ease;
  }
  
  .navbar.scrolled {
    background-color: rgba(10, 25, 47, 0.9);
    box-shadow: 0 10px 30px -10px rgba(2, 12, 27, 0.7);
    padding: 15px 40px;
  }
  
  .logo {
    font-size: 24px;
    font-weight: 700;
    color: #f4fcfa;
    cursor: pointer;
  }
  
  .nav-links {
    display: flex;
    list-style: none;
    gap: 30px;
    align-items: center;
  }
  
  .nav-links a, .nav-link {
    color: #ccd6f6;
    text-decoration: none;
    font-weight: 500;
    font-size: 16px;
    padding: 10px 0;
    position: relative;
    transition: color 0.3s ease;
  }
  
  .nav-links a:hover, .nav-link:hover {
    color: #64ffda;
  }
  
  .nav-links a::before, .nav-link::before {
    content: '';
    position: absolute;
    width: 0;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: #64ffda;
    transition: width 0.3s ease;
  }
  
  .nav-links a:hover::before, .nav-link:hover::before {
    width: 100%;
  }
  
  /* Active link style */
  .nav-links a.active, .nav-link.active {
    color: #64ffda;
  }
  
  .nav-links a.active::before, .nav-link.active::before {
    width: 100%;
  }
  
  /* Mobile Menu */
  .menu-icon {
    display: none;
    cursor: pointer;
    z-index: 1001;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .navbar {
      padding: 20px;
    }
  
    .nav-links {
      position: fixed;
      top: 0;
      right: -100%;
      width: 70%;
      height: 100vh;
      background-color: #0a192f;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      transition: right 0.5s ease;
      padding-top: 80px;
    }
  
    .nav-links.active {
      right: 0;
    }
  
    .menu-icon {
      display: block;
    }
  
    .nav-links li {
      margin: 20px 0;
    }
  }