/* Chatbot Container - Increased Size */
.chatbot-container {
  max-width: 1000px;
  margin: 50px auto;
  padding: 30px;
  background-color: #f9f9f9;
  border-radius: 15px;
  box-shadow: 0 5px 25px rgba(0,0,0,0.15);
  font-family: 'Poppins', sans-serif;
  min-height: 700px;
  display: flex;
  flex-direction: column;
}

/* Back Link */
.chatbot-container a {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  color: #4e54c8;
  text-decoration: none;
  font-weight: 500;
  margin-bottom: 25px;
  transition: color 0.3s;
  font-size: 1.1rem;
}

.chatbot-container a:hover {
  color: #3a3f9e;
}

/* Header */
.chatbot-container h3 {
  color: #333;
  font-size: 28px;
  margin-bottom: 40px;
  text-align: center;
  position: relative;
}

.chatbot-container h3::after {
  content: '';
  position: absolute;
  bottom: -15px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: #4e54c8;
}

/* Chat Window */
.chat-window {
  height: 500px;
  padding: 25px;
  background: white;
  border-radius: 10px;
  margin-bottom: 30px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Messages */
.user-msg, .bot-msg {
  max-width: 80%;
  padding: 15px 20px;
  border-radius: 18px;
  line-height: 1.6;
  font-size: 1.05rem;
  position: relative;
}

.user-msg {
  background-color: #4e54c8;
  color: white;
  margin-left: auto;
  border-bottom-right-radius: 4px;
}

.bot-msg {
  background-color: white;
  color: #333;
  border: 1px solid #e5e5e7;
  margin-right: auto;
  border-bottom-left-radius: 4px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
}

/* Message Time */
.message-time {
  font-size: 0.8rem;
  color: rgba(255,255,255,0.7);
  margin-top: 8px;
  text-align: right;
}

.bot-msg .message-time {
  color: #8e8e93;
}

/* Input Area */
.input-container {
  display: flex;
  gap: 15px;
  margin-top: auto;
}

.input-container input {
  flex: 1;
  padding: 15px 25px;
  border: 1px solid #ddd;
  border-radius: 30px;
  font-family: 'Poppins', sans-serif;
  font-size: 1.1rem;
  outline: none;
  transition: all 0.3s;
}

.input-container input:focus {
  border-color: #4e54c8;
  box-shadow: 0 0 0 3px rgba(78, 84, 200, 0.2);
}

.input-container button {
  background: #4e54c8;
  color: white;
  border: none;
  padding: 0 30px;
  border-radius: 30px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.input-container button:hover {
  background: #3a3f9e;
  transform: translateY(-2px);
}

.input-container button:disabled {
  background: #a1a4d6;
  transform: none;
  cursor: not-allowed;
}

/* Loading Indicator */
.typing-indicator {
  display: flex;
  gap: 5px;
  padding: 10px 15px;
}

.typing-dot {
  width: 8px;
  height: 8px;
  background-color: #8e8e93;
  border-radius: 50%;
  animation: typingAnimation 1.4s infinite ease-in-out;
}

.typing-dot:nth-child(1) {
  animation-delay: 0s;
}
.typing-dot:nth-child(2) {
  animation-delay: 0.2s;
}
.typing-dot:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes typingAnimation {
  0%, 60%, 100% { transform: translateY(0); }
  30% { transform: translateY(-5px); }
}

/* Responsive Design */
@media (max-width: 992px) {
  .chatbot-container {
    margin: 30px;
    min-height: 600px;
  }
  
  .chat-window {
    height: 400px;
  }
}

@media (max-width: 768px) {
  .chatbot-container {
    margin: 20px;
    padding: 20px;
    min-height: 500px;
  }
  
  .chatbot-container h3 {
    font-size: 24px;
  }
  
  .user-msg, .bot-msg {
    max-width: 85%;
    font-size: 1rem;
    padding: 12px 16px;
  }
  
  .input-container input {
    padding: 12px 20px;
    font-size: 1rem;
  }
}

@media (max-width: 480px) {
  .chatbot-container {
    margin: 10px;
    padding: 15px;
  }
  
  .input-container {
    gap: 10px;
  }
  
  .input-container button {
    padding: 0 20px;
  }
}